import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
   providedIn: 'root'
})
export class PokemonDataService {
   private apiUrl = environment.api;

   constructor(private http: HttpClient) {}

   // Headers
   httpOptions = {
      headers: new HttpHeaders({
         'Content-Type': 'application/json'
      })
   };

   getAllPokemonGamesGenerations(): Observable<any> {
      return this.http.get<any>(`${this.apiUrl}/generation`);
   }

   getGameGenerationDetailsByName(name: string): Observable<any> {
      return this.http.get<any>(`${this.apiUrl}/generation/${name}`);
   }
}
